/* noto-serif-tc-regular - chinese-traditional */
@font-face {
    font-family: 'Noto Serif TC';
    font-style: normal;
    font-weight: 400;
    src: url('../fonts/noto-serif-tc-v7-chinese-traditional-regular.eot'); /* IE9 Compat Modes */
    src: local('Noto Serif TC'), local('NotoSerifTC-Regular'),
         url('../fonts/noto-serif-tc-v7-chinese-traditional-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
         url('../fonts/noto-serif-tc-v7-chinese-traditional-regular.woff2') format('woff2'), /* Super Modern Browsers */
         url('../fonts/noto-serif-tc-v7-chinese-traditional-regular.woff') format('woff'), /* Modern Browsers */
         url('../fonts/noto-serif-tc-v7-chinese-traditional-regular.ttf') format('truetype'), /* Safari, Android, iOS */
         url('../fonts/noto-serif-tc-v7-chinese-traditional-regular.svg#NotoSerifTC') format('svg'); /* Legacy iOS */
  }
  /* noto-serif-tc-900 - chinese-traditional */
  @font-face {
    font-family: 'Noto Serif TC';
    font-style: normal;
    font-weight: 900;
    src: url('../fonts/noto-serif-tc-v7-chinese-traditional-900.eot'); /* IE9 Compat Modes */
    src: local('Noto Serif TC Black'), local('NotoSerifTC-Black'),
         url('../fonts/noto-serif-tc-v7-chinese-traditional-900.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
         url('../fonts/noto-serif-tc-v7-chinese-traditional-900.woff2') format('woff2'), /* Super Modern Browsers */
         url('../fonts/noto-serif-tc-v7-chinese-traditional-900.woff') format('woff'), /* Modern Browsers */
         url('../fonts/noto-serif-tc-v7-chinese-traditional-900.ttf') format('truetype'), /* Safari, Android, iOS */
         url('../fonts/noto-serif-tc-v7-chinese-traditional-900.svg#NotoSerifTC') format('svg'); /* Legacy iOS */
  }